/* #region from prime */
.bg-my.rmdp-wrapper {
  background-color: #ebf1f6;
  border: 1px solid #8798ad;
}

.bg-my .rmdp-calendar {
  padding: 0 !important;
}

.bg-my .rmdp-header,
.bg-my .rmdp-panel-header {
  border-bottom: 1px solid #8798ad;
  margin-top: 0 !important;
}

.bg-my .rmdp-time-picker {
  border-top: 1px solid #8798ad;
  margin-bottom: 0 !important;
}

.bg-my .rmdp-panel-body li {
  border-radius: 5px;
}

.bg-my .rmdp-border-top {
  border-top: 1px solid #8798ad !important;
}

.bg-my .rmdp-border-bottom {
  border-bottom: 1px solid #8798ad !important;
}

.bg-my .rmdp-border-left {
  border-left: 1px solid #8798ad !important;
}

.bg-my .rmdp-border-right {
  border-right: 1px solid #8798ad !important;
}

.bg-my.ep-arrow::after {
  box-shadow: 0 0 6px #404050 !important;
  /* box-shadow: 2px 2px 2px -1px #39566b; */
}
/* #endregion from prime */

